.icofont-ul > li {
  position: relative;
  line-height: 2em;
}
.icofont-ul > li .icofont {
  display: inline-block;
  vertical-align: middle;
}
.icofont-border {
  border: solid 0.08em #f1f1f1;
  border-radius: 0.1em;
  padding: 0.2em 0.25em 0.15em;
}
